.container {
  max-width: 1100px;
  margin: auto;
  overflow: hidden;
  padding: 0 2rem;
  margin-top: 6rem;
  margin-bottom: 3rem; }

.x-large {
  font-size: 4rem;
  line-height: 1.2;
  margin-bottom: 1rem; }

.large {
  font-size: 3rem;
  line-height: 1.2;
  margin-bottom: 1rem; }

.lead {
  font-size: 1.5rem;
  margin-bottom: 1rem; }

.text-primary {
  color: #17a2b8; }

.round-img {
  border-radius: 50%; }

.bg-primary {
  background-color: #17a2b8;
  color: #fff; }

.bg-light {
  background-color: #f4f4f4;
  color: #333;
  border: 1px solid #ccc; }

.bg-dark {
  background-color: #343a40;
  color: #fff; }

.bg-success {
  background-color: #28a745;
  color: #fff; }

.bg-danger {
  background-color: #dc3545;
  color: #fff; }

.bg-white {
  background-color: #fff;
  color: #333;
  border: 1px solid #ccc; }

.btn {
  display: inline-block;
  background: #f4f4f4;
  color: #333;
  padding: 0.4rem 1.3rem;
  border: none;
  cursor: pointer;
  font-size: 1rem;
  margin-right: 0.5rem;
  outline: none;
  transition: all 0.2s ease-in; }
  .btn.btn-primary {
    background-color: #17a2b8;
    color: #fff; }
    .btn.btn-primary:hover {
      background: #1ab6cf; }
  .btn.btn-dark {
    background-color: #343a40;
    color: #fff; }
    .btn.btn-dark:hover {
      background: #3f474e; }
  .btn.btn-success {
    background-color: #28a745;
    color: #fff; }
    .btn.btn-success:hover {
      background: #2dbc4e; }
  .btn.btn-danger {
    background-color: #dc3545;
    color: #fff; }
    .btn.btn-danger:hover {
      background: #e04b59; }
  .btn:hover {
    background: #626d78;
    color: #fff; }

.alert {
  padding: 0.8rem;
  margin: 1rem;
  opacity: 0.9;
  background: #f4f4f4;
  color: #333; }
  .alert.alert-primary {
    background-color: #17a2b8;
    color: #fff; }
  .alert.alert-dark {
    background-color: #343a40;
    color: #fff; }
  .alert.alert-danger {
    background-color: #dc3545;
    color: #fff; }
  .alert.alert-success {
    background-color: #28a745;
    color: #fff; }

.dark-overlay {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.7); }

.form-group {
  margin: 1.2rem 0; }

.form-text {
  display: block;
  margin-top: 0.3rem;
  color: #888; }

.form input[type='text'],
.form input[type='email'],
.form input[type='password'],
.form input[type='date'],
.form select,
.form textarea {
  display: block;
  width: 100%;
  padding: 0.4rem;
  font-size: 1.2rem;
  border: 1px solid #ccc; }

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

body {
  background-color: #fff;
  font-family: 'Raleway', sans-serif;
  font-size: 1rem;
  line-height: 1.6;
  color: #333; }

a {
  text-decoration: none;
  color: #17a2b8; }

ul {
  list-style: none; }

img {
  width: 100%; }

.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.7rem 2rem;
  position: fixed;
  width: 100%;
  z-index: 1;
  top: 0;
  border-bottom: 1px solid #17a2b8;
  opacity: 0.9; }
  .navbar ul {
    display: flex; }
  .navbar a {
    color: #fff;
    padding: 0.45rem;
    margin: 0 0.25rem; }
    .navbar a:hover {
      color: #17a2b8; }

.landing {
  position: relative;
  background: url("../img/showcase.jpg") no-repeat center center/cover;
  height: 100vh; }
  .landing-inner {
    color: #fff;
    height: 100%;
    display: flex;
    flex-direction: column;
    width: 80%;
    margin: auto;
    align-items: center;
    justify-content: center;
    text-align: center; }

.profile {
  display: grid;
  grid-template-columns: 2fr 4fr 2fr;
  gap: 2rem;
  align-items: center;
  padding: 1rem;
  line-height: 1.8;
  margin-bottom: 1rem; }

.profile-grid {
  display: grid;
  grid-template-areas: 'top top' 'about about' 'exp edu' 'github github';
  gap: 1rem; }
  .profile-grid .profile-top {
    grid-area: top;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center; }
    .profile-grid .profile-top img {
      width: 250px; }
    .profile-grid .profile-top .icons a {
      color: #fff;
      margin: 0 0.3rem; }
      .profile-grid .profile-top .icons a:hover {
        color: #343a40; }
  .profile-grid .profile-about {
    grid-area: about; }
  .profile-grid .profile-exp {
    grid-area: exp; }
  .profile-grid .profile-edu {
    grid-area: edu; }
  .profile-grid .profile-github {
    grid-area: github; }
